<template>
    <div class="todo-item" v-bind:class="{'is-complete':inputParameter.completed}">
        <!--bind to todo-item, BUT also bind to is-completed IF inputParameter is completed (check app data)-->
        <p>
            <input type="checkbox" v-on:change="markComplete">
            {{inputParameter.title}}
            <button class="del" v-on:click="$emit('del-todo-from-item', inputParameter.id)">X</button>
            <!--(1) Choose any name you want, but be sure to export it in PROPS-->
        </p>
    </div>
</template>

<script>
export default {
    name: "TodoItem",
    props:["inputParameter"],    //(2) export inputParameter as a property of this file to Todos.vue
    methods:{
        markComplete(){
            this.inputParameter.completed = !this.inputParameter.completed;
        }
    }
}
</script>



<style scoped>
    .todo-item {
        background: #f4f4f4;
        padding: 10px;
        border-bottom: 1px #ccc dotted;
    }
    .is-complete {
        text-decoration: line-through;
    }
    .del {
        background: #ff0000;
        color: #fff;
        border: none;
        padding: 5px 9px;
        border-radius: 50%;
        cursor: pointer;
        float: right;
    }
</style>


