<template>
   <div>
       <div v-bind:key="todo.id" v-for="todo in todos">
           <TodoItem v-bind:inputParameter="todo" v-on:del-todo-from-item="$emit('del-todo-from-main', todo.id)"/>         
           <!--(3) recieve the PROP you exported from TodoItem.vue here and bind it in the for loop of the DIV-->
       </div>
   </div>
</template>

<script>
  //exported data: 
  // • components are used within the same file in the template block
  // • props are used in other files.
import TodoItem from './TodoItem.vue';
export default {
  name: "Todos",
  components: {
      TodoItem
  },
  props:["todos"]
}
</script>


