<template>
    <div>
        <form @submit="addTodo">
            <input type="text" v-model="title" name="title" placeholder="Add todo..."> 
            <!--v-model is needed to be able to add the input to the COMPONENT DATABASE data() name title-->
            <input type="submit" value="Submit" class="btn">
        </form>
    </div>
</template>

<script>
import uuid from 'uuid'; // need to be installed first via npm i uuid, handles auto generating ids
export default {
    name:"AddTodo",
    data(){
        return{
            title : ''
        }
    },
    methods:{
        addTodo(e){
            e.preventDefault(); //prevent reloading the webpage (callbacks)
            console.log(e);
            const newTodo = {
                id: uuid.v4(), // auto generate id
                title : this.title,
                completed : false
            }
            //send up to parent (App.vue)
            this.$emit('add-todo', newTodo);
            this.title = '';
        }
    }
}
</script>

<style scoped>
    form {
    display: flex;
  }
  input[type="text"] {
    flex: 10;
    padding: 5px;
  }
  input[type="submit"] {
    flex: 2;
  }
</style>


