<template>
  <div id="app">
    <Header />
    <AddTodo v-on:add-todo="affectDatabase"/>
    <Todos v-bind:todos="todosData" v-on:del-todo-from-main="deleteTodo"/>
    <!--Bind todosData from below that is being exported-->
  </div>
</template>

<script>
import Todos from './components/Todos.vue'; //import from that file as an alias name
import AddTodo from './components/AddTodo.vue';
import Header from './components/layout/Header.vue';

export default {
  name: 'app',
  components: {
    Todos,
    Header,
    AddTodo
  },
  data(){
    return{
      todosData:[
        /*{
          id:1,
          title: "TODO one",
          completed: false
        },
        {
          id:2,
          title: "TODO two",
          completed: true
        },
        {
          id:3,
          title: "TODO three",
          completed: false
        }*/
      ]
    }
  },
  methods:{
    deleteTodo(id){
      this.todosData = this.todosData.filter(i => i.id !== id);
    },
    affectDatabase(newTodo){
      this.todosData = [...this.todosData, newTodo];
    }
  }
}
</script>

